import { Component, ViewChild } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { SEOService } from './services/seo/seo.service';
import { TranslateService } from '@ngx-translate/core';
import { BreakpointObserver } from '@angular/cdk/layout'
import { MatSidenav } from '@angular/material/sidenav';
import { delay } from 'rxjs/operators';

declare let gtag: Function;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})

export class AppComponent {
  @ViewChild(MatSidenav)
  sidenav!: MatSidenav;

  lang;

  constructor(public router: Router, private _seoService: SEOService, private _translateService: TranslateService, private observer: BreakpointObserver) {
    this._translateService.addLangs(['zh', 'en', 'ko']);
    this._translateService.setDefaultLang('zh');
  }

  ngOnInit() {
    // set i18n, default language to Chinese
    let browserLang = navigator.language.substr(0, 2);
    let lang = localStorage.getItem('language');
    let l = 'zh';
    //if no user choice on language, set language based on browser language
    if (!lang || lang == '') {
      if (browserLang == 'en' || browserLang == 'zh' || browserLang == 'ko') {
        l = browserLang;
      }
    } else {
      //prevent wrong language exist in localStorage
      if (lang == 'en' || lang == 'zh' || lang == 'ko') {
        l = lang;
      }
    }
    localStorage.setItem('language', l)
    this._translateService.use(l);
    this.lang = l;

    //for google analystics
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.setTitle(event.urlAfterRedirects);
        gtag('config', 'G-R56QKY4DDW',
          {
            'page_title': this._seoService.getTitle(),
            'page_path': event.urlAfterRedirects
          });
      }
    })
    this._seoService.loadTags()
  }

  ngAfterViewInit() {
    //responsive side nav bar
    this.observer
      .observe(['(max-width: 800px)'])
      .pipe(delay(1))
      .subscribe((res) => {
        if (res.matches) {
          this.sidenav.mode = 'over';
          this.sidenav.close();
        } else {
          this.sidenav.mode = 'side';
          this.sidenav.open();
        }
      });
  }

  //change the title of page while routing
  setTitle(url: string) {
    let home = ["", "Home", ""];
    let cards = ["", "Cards", ""];
    let cardCal = ["", "Power Calculator", " "];
    let rssCal = ["", "Resource Calculator", " "];
    let cardSele = ["", "Card Selection", " "];
    let skills = ["", "Skills", ""];
    let other = ["", "Other", ""];
    let visionHistory = ["", "Vision History", " "];
    let merchList = ["", "Official Merch List", " "];
    let updateLog = ["", "Update Log", " "];
    let i = 0;
    if ('en' == this.lang) {
      i = 1;
    } else if ('ko' == this.lang) {
      i = 2;
    }
    let s;
    switch (url) {
      case '/':
        s = home[i];
        break;
      case '/cards':
        s = cards[i];
        break;
      case '/card-calculator':
        s = cardCal[i];
        break;
      case '/card-rss-calculator':
        s = rssCal[i];
        break;
      case '/card-selection':
        s = cardSele[i];
        break;
      case '/skills':
        s = skills[i];
        break;
      case '/other':
        s = other[i];
        break;
      case '/vision-history':
        s = visionHistory[i];
        break;
      case '/merch-list':
        s = merchList[i];
        break;
      case '/update-log':
        s = updateLog[i];
        break;
    }
    if (s) {
      this._seoService.setTitle(s);
    }
  }

  toTopOfScreen(){
    let content = document.getElementById("content-container");
  }

  changeLanguage(){
    localStorage.setItem('language', this.lang)
    this._translateService.use(localStorage.getItem('language'))
    location.reload()
  }

}
